import View from '../View';
import toggle from '../toggle';

export default new View({
  container: document.querySelector('.main-container'),
  events: [{
    type: 'click',
    target: '.main-container',
    method: function() {
      toggle('.main-container', 'main-container-reverse');
    }
  }],
  template: `
  <div class="main-item-row">
    <div class="main-item">A</div>
    <div class="main-item">B</div>
  </div>
  <div class="main-item-row">
    <div class="main-item">C</div>
    <div class="main-item">D</div>
  </div>
  `
});



