import View from '../View';
import request from '../request';

class AccordionView extends View {
  constructor(options) {
    super(options);
  }
  onSuccess(res) {
    let index = 0;
    let items = res.items;
    for(; index < 5; index += 1) {
      let item = items[index];
      let src = item.media.m;
      let title = item.title;
      this.template += `<div class="accordion-item" style="background-image:url(${src})"><span class="blind">${title}</span></div>`;
      this.datas.push(item);
    }
    this.updateView();
  }
  viewDidMount() {
    this.template = '';
    request("http://api.flickr.com/services/feeds/photos_public.gne?format=json&jsoncallback=", "app.AccordionView.onSuccess");
  }
}

export default new AccordionView({
  container: document.querySelector('.accordion'),
  template: `<div class="accordion-item"></div>`,
});



