class View {
  constructor(options) {
    this.datas = [];
    this.container = options.container || {};
    this.template = options.template || '';
    this.events = options.events || null;
    return this;
  }
  _bindEvents() {
    if(!!this.events) {
      this.events.forEach((o) => {
        document.querySelector(o.target).addEventListener(o.type, o.method);
      })
    }
    return this;
  }
  viewWillMount() {

  }
  viewDidMount() {

  }
  updateView() {
    this.container.innerHTML = this.template;
    this._bindEvents();
    return this;
  }
  render() {
    this.viewWillMount();
    this.container.innerHTML = this.template;
    this._bindEvents();
    this.viewDidMount();
    return this;
  }
}

export default View;


